.exclusive-features {
    padding: 100px 0;
    background-color: #f8f9fa;
    .section-header {
        text-align: center;
        margin-bottom: 40px;
        .section-title {
            font-style: normal;
            font-weight: 600;
            font-size: 40px;
            line-height: 35px;
            color: #000000;
            margin-bottom: 20px;
        }
        .section-subtitle {
            color: #666;
            font-size: 1.1rem;
            max-width: 669px;
            margin: 0 auto;
        }
    }
    .feature-card {
        background: white;
        border-radius: 20px;
        overflow: hidden;
        border: 1px solid #D3D3D3;
        margin-bottom: 30px;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
        height: 100%;
        &.style-1 {
            background-color: #FFFAFE;
            @media only screen and (max-width: 1200px) {
                flex-direction: column;
            }
        }
        &.style-2 {
            background-color: #F7F5FF;
            @media only screen and (max-width: 1200px) {
                flex-direction: column;
                .feature-image{
                    width: 100% !important;
                }
            }
        }
        .feature-content {
            padding: 60px 0px 20px 30px;
            .feature-title {
                margin-bottom: 15px;
                img {
                    width: 118px;
                    height: 24px;
                    &.style-1 {
                        padding-right: 32px;
                    }
                }
                h3 {
                    font-weight: 600;
                    font-size: 30px;
                    line-height: 35px;
                    color: #000000;
                }
            }
            .feature-description {
                color: #666;
                margin-bottom: 20px;
                font-size: 0.95rem;
                line-height: 1.6;
            }
            .feature-tags {
                display: flex;
                flex-wrap: wrap;
                gap: 10px;
                .feature-tag {
                    border-radius: 20px;
                    font-size: 0.85rem;
                    color: #666;
                    display: flex;
                    align-items: self-start;
                    gap: 5px;
                    flex-direction: column;
                    ul {
                        gap: 24px;
                        li {
                            font-weight: 500;
                            font-size: 15px;
                            line-height: 22px;
                            color: #000000;
                            img {
                                margin-right: 5px;
                            }
                        }
                    }
                }
            }
        }
        .feature-image {
            position: relative;
            top: 65px;
            width: 514px;
            height: 351px;
        }
    }
    .plugin-card {
        background: linear-gradient(45deg, #4158D0, #6C63FF);
        color: white;
        border-radius: 20px;
        padding: 70px 30px;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        &.style-1 {
            background: #004BFE;
            position: relative;
            .shapes {
                position: absolute;
                top: 24px;
                right: 24px;
            }
        }
        &.style-2 {
            background: #520CFE;
        }
        .plugin-icon {
            width: 115px;
            height: 90px;
            margin-bottom: 20px;
        }
        .plugin-title {
            font-style: normal;
            font-weight: 500;
            font-size: 26px;
            line-height: 30px;
            color: #FFFFFF;
            margin-bottom: 10px;
            span {
                font-weight: 300;
            }
        }
        .plugin-description {
            font-style: normal;
            font-weight: 400;
            font-size: 15px;
            line-height: 24px;
            color: #FFFFFF;
        }
    }
    @media (max-width: 991px) {
        .exclusive-features {
            padding: 60px 0;
        }
        
        .section-title {
            font-size: 2rem;
        }
    }
}



