/**
 * Layout - Header.
 */
 * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* Navigation */
.hero-section {
  background-image: url("../images/hero/herobg.png"); 
  background-size: cover; 
  background-position: center; 
  background-repeat: no-repeat; 
  &::before {
    content: "";
    position: absolute;
    left: 0; 
    bottom: 0;
    right: 0;
    top: 0;
    width: 100%; 
    height: auto; 
    background-image: url("../images/hero/bottom-shapes.png");
    z-index: 1;
    background-size: contain; 
    background-repeat: no-repeat;
    background-position: center bottom;
  }
}
.nav {
  position: relative;
  z-index: 5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 0px;
  margin: 0px 58px;
}

.logo {
  font-size: 24px;
  font-weight: bold;
}

.nav-links {
  display: flex;
  gap: 80px;
}

.nav-links a {
  color: white;
  text-decoration: none;
  transition: opacity 0.3s;
}

.nav-links a:hover {
  opacity: 0.8;
}

.purchase-btn-2 {
  padding: 10px 24px;
  border: none;
  border-radius: 50px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 500px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s;
  &.style-1 {
    border: 1px solid #939393;
    background: transparent;
    &:hover {
      background-color: #FF6C23;
      color: white;
      border: 1px solid transparent;
    }
  }
  &.style-2 {
    &:hover {
      background-color: #ffffff;
      color: rgb(0, 0, 0);
    }
  }
  @media only screen and (max-width: 768px) {
    display:none;
}
}

.purchase-btn:hover {
  background: white;
  color: #1a0b2e;
}

/* Hero Content */
.hero {
  text-align: center;
  position: relative;
  padding-top: 100px;
  z-index: 2;
}

.text-3d {
  position: relative;
  bottom: -55px;
}

.hero-title {
  font-size: 48px;
  max-width: 904px;
  margin: 0 auto 32px;
  line-height: 1.2;
  font-size: 3.5rem;
  font-weight: 600;
  margin-bottom: 30px;
  line-height: 1.2;
  color: white;
  position: relative;
  z-index: 5;
}

.hero-subtitle {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  max-width: 650px;
  margin: 0 auto 48px;
  color: white;
}

.cta-buttons {
  display: flex;
  gap: 24px;
  justify-content: center;
}

.btn-primary-1 {
  padding: 16px 32px;
  border-radius: 50px;
  background: #FF6C23;
  color: white;
  border: none;
  cursor: pointer;
  transition: background 0.3s;
  outline: none;
}
.btn-primary-1:hover {
  background: #f4511e;
  color: white;
}

/* Pre-designed Badge */
.pre-designed-badge {
  position: absolute;
  left: 40px;
    top: 125%;
  z-index: 2;
  transform: translateY(-50%);
  width: 140px;
  height: 140px;
  background: #FF0037;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: center;
  padding: 10px;
  @media only screen and (max-width: 1200px) {
    display:none;
}
}

.badge-number {
  font-size: 32px;
  font-weight: bold;
}

.badge-text {
  font-size: 15px;
  line-height: 1.2;
  margin-bottom: 5px;
}

/* Two-Part Marquee Slider */
.marquee-slider {
  position: relative;
  bottom: -150px;
  overflow: hidden;
}

.marquee-container {
  display: flex;
  width: 286%; 
  @media only screen and (max-width: 768px) {
    width: 580%;
}
}

.marquee-container.right {
  animation: marquee-right 30s linear infinite;
}

.marquee-container.left {
  animation: marquee-left 30s linear infinite;
}

@keyframes marquee-right {
  0% { transform: translateX(0); }
  100% { transform: translateX(-50%); }
}

@keyframes marquee-left {
  0% { transform: translateX(-50%); }
  100% { transform: translateX(0); }
}

.marquee-container:hover {
  animation-play-state: paused;
}

.template-card {
  height: 365px;
  margin-right: 24px;
  border-radius: 20px;
  overflow: hidden;
  transition: transform 0.3s;
}

.template-card:hover {
  transform: translateY(-10px);
}

.template-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Power Elite Badge */
.power-elite {
  position: fixed;
  right: 40px;
  top: 50%;
  transform: translateY(-50%);
  width: 80px;
  height: 80px;
}

/* Mobile Menu Styles */
.menu-toggle {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 21px;
  cursor: pointer;
}

.menu-toggle span {
  display: block;
  width: 100%;
  height: 3px;
  background-color: white;
  transition: all 0.3s;
}

.mobile-menu {
  position: fixed;
  top: 0;
  right: -100%; /* Start hidden (off-screen) */
  width: 100%;
  height: 100%;
  background-color: #0000006b;
  background-image: linear-gradient(180deg, #000000 28%, rgba(95, 99, 97, 0) 341%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: right 0.5s ease-in-out; /* Smooth transition for the "right" property */
  z-index: 1000;
}

.mobile-menu.active {
  right: 0; /* Move into view when active */
}


.mobile-menu.active {
  right: 0;
}

.mobile-menu a {
  color: white;
  text-decoration: none;
  font-size: 24px;
  margin: 20px 0;
  transition: opacity 0.3s;
}

.mobile-menu a:hover {
  opacity: 0.8;
}

.close-menu {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 50px;
  color: white;
  cursor: pointer;
}

@media (max-width: 768px) {
  .nav-links, .nav .purchase-btn {
      display: none;
  }

  .menu-toggle {
      display: flex;
  }

  .text-3d {
      font-size: 80px;
  }

  .hero-title {
      font-size: 32px;
  }

  .pre-designed-badge {
      width: 90px;
      height: 90px;
      left: 20px;
  }

  .power-elite {
      width: 60px;
      height: 60px;
      right: 20px;
  }

  .cta-buttons {
      flex-direction: column;
      align-items: center;
  }

  .template-card {
      flex: 0 0 250px;
      height: 187px;
  }
}






.headroom {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  transform: translateY(0);
  transition: transform 0.5s ease-in-out, background-color 0.3s ease-in-out, opacity 0.5s ease-in-out;
  will-change: transform, background-color, opacity;
  background-color: transparent; /* Transparent when at the top */
  
}

.headroom--scrolled {
  background-color: rgba(0, 0, 0, 0.6); /* Semi-transparent black background */
  background-image: linear-gradient(180deg, #000000 0%, rgba(59, 59, 59, 0) 100%);
  transition: background-color 0.3s ease-in-out;
  transition: transform 0.5s ease-in-out, background-color 0.5s ease-in-out, opacity 0.5s ease-in-out;
}

.headroom-pinned {
  transform: translateY(-100%);
  transition: transform 0.5s ease-in-out, background-color 0.5s ease-in-out, opacity 0.5s ease-in-out;
  opacity: 0.9; /* Adjust opacity if necessary */
  transform: translateY(0);
}

.headroom-unpinned {
  transform: translateY(-100%);
  box-shadow: none;
}
.nav-links a.active {
  color: #FF6C23;
}
